<template>
  <div id="about-us">
    <head-top title="关于我们"></head-top>

    <div class="main">
      <div class="info">
        <img src="../../assets/logoqianse.jpg" alt />

        <div class="name">智慧教室互动课堂</div>
        <div class="version">Version {{version}}</div>
      </div>
      <div class="download">
        <img src="../../assets/erweima.jpg" alt />
        <div class="name">扫描二维码下载app</div>
        <div class="version">福建睿和科技有限公司所有</div>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import config from "./../../../package.json";

export default {
  name: "",
  components: { headTop },
  props: {},
  data() {
    return {
      version: config.version
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang='less'>
#about-us {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 1);

  .main {
    width: 100%;
    height: 90%;
    .name {
      font-size: 1.8rem;
      font-family: MicrosoftYaHei;
      color: rgba(51, 51, 51, 1);

      margin-top: 21px;
    }
    .version {
      font-size: 1.4rem;
      font-family: MicrosoftYaHei;
      color: rgba(102, 102, 102, 1);
      margin-top: 9px;
    }
    .info {
      img {
        width: 70px;
        height: 70px;
        margin-top: 82px;
      }
    }

    .download {
      margin-top: 193px;
      img {
        width: 100px;
        height: 100px;
      }
    }
  }
}
</style>


